import React, {Component} from "react";

class OptionContainer extends Component{
    constructor(props){
        super(props);
        console.log(this.props);
    }
    setAdminBody = (e) => {
        e.preventDefault();
        console.log("option clicked",e);
    } 
    render(){
        let {admin} = this.props;
        let adminArray = Object.values(admin);
        return(
            <div>
                {adminArray.map(item => (
                        <div 
                            key={item.id}
                            className="option"
                            onClick = {this.setAdminBody}
                            >
                            {item.name}
                        </div>
                    ))}
                <style jsx>{`
                div{
                    margin-top: 5px;
                }
                .option{
                    border: 4px outset #0ff;
                    width: 90%;
                    margin-left:2.5%;
                    background-color: blue;
                    border-radius: 15px;
                    height: 30px;
                    line-height: 30px;
                    color: white;
                    font-size: 19px;
                }
                `}</style>
            </div>
        )
    }
}


export default OptionContainer;